import React, { Component } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import SideNav, {
  Toggle,
  Nav,
  NavItem,
  NavIcon,
  NavText
} from "@trendmicro/react-sidenav";
import "@trendmicro/react-sidenav/dist/react-sidenav.css";
import Home from "../home";
import About from "../about";
import AddProduct from "../AddProduct";

class Vendor extends Component {
  render() {
    return (
      <div className="ml-5" style={{ minHeight: "100vh" }}>
        <Router>
          <Route
            render={({ location, history }) => (
              <React.Fragment>
                <SideNav
                  onSelect={selected => {
                    const to = "/Vendor/" + selected;
                    if (location.pathname !== to) {
                      history.push(to);
                    }
                  }}
                >
                  <SideNav.Toggle />
                  <SideNav.Nav defaultSelected="home">
                    <NavItem eventKey="addproduct">
                      <NavIcon>
                        <i
                          className="fa fa-fw fa-plus"
                          style={{ fontSize: "1.75em" }}
                        />
                      </NavIcon>
                      <NavText>Home</NavText>
                    </NavItem>
                    <NavItem eventKey="devices">
                      <NavIcon>
                        <i
                          className="fas fa-wifi"
                          style={{ fontSize: "1.75em" }}
                        />
                      </NavIcon>
                      <NavText>Devices</NavText>
                    </NavItem>
                  </SideNav.Nav>
                </SideNav>
                <main className="w-auto ml-1">
                  <Route
                    path="/Vendor/"
                    exact
                    component={props => () => {
                      return <div />;
                    }}
                  />
                  <Route
                    path="/Vendor/addproduct"
                    component={props => <AddProduct />}
                  />
                  <Route
                    path="/Vendor/devices"
                    component={props => <About />}
                  />
                </main>
              </React.Fragment>
            )}
          />
        </Router>
      </div>
    );
  }
}

export default Vendor;
